<template>
  <div style="padding-bottom:500px">
      <h4> Indeks Prestasi Kumulatif (IPK) </h4> <br>

  <table class="table">
  <thead>
    <tr>
      <th scope="col">Kode Matakuliah</th>
      <th scope="col">Nama Matakuliah</th>
      <th scope="col">Nilai Akhir</th>
    </tr>
  </thead>
  <tbody v-for="(course, index) in courses" :key="index">
    <tr>
      <td>{{course.kode_makul}} </td>
      <td>{{course.nama}} </td>
      <td>{{course.nilai_akhir}} </td>
    </tr>
  
  </tbody>

 
</table>

</div>
   
</template>

<script>
import GradeDataService from "../services/GradeDataService";

export default {
  name: "courses-list",
  data() {
    return {
      courses: [],
      currentCourse: null,
      currentIndex: -1,
      title: ""
    };
  },
  methods: {
    
  
    retrieveCourses() {
      GradeDataService.getNilai()
        .then(response => {
          this.courses = response.data.result;
        })
        .catch(e => {
          console.log(e);
        });
      
    },

    refreshList() {
      this.retrievecourses();
      this.currentCourse = null;
      this.currentIndex = -1;
    }
  },
  mounted() {
    this.retrieveCourses();
  }
};
</script>

<style>
.list {
  text-align: left;
  max-width: 750px;
  margin: auto;
}
</style>
