import AutentikasiHeader from './AutentikasiHeader';
import axios from 'axios';

class GradeDataService {

  async getNilai() {
    let user = JSON.parse(localStorage.getItem('jwt'));
    return await axios.get('/api/v2/grade/'+user.result.no_induk+'', { headers: AutentikasiHeader() });
  }

}

export default new GradeDataService();
